import { Helmet } from 'react-helmet-async';
// react
import { useEffect, useMemo, useState } from 'react';
// MUI
import LoadingButton from '@mui/lab/LoadingButton/LoadingButton';
import Card from '@mui/material/Card';
import Grid from '@mui/material/Grid';
import MenuItem from '@mui/material/MenuItem';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
// form
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import * as Yup from 'yup';
// component
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs';
import FormProvider, {
  BlockItem,
  RHFSelect,
  RHFSwitch,
  RHFTextField,
} from 'src/components/hook-form';
import { useSnackbar } from 'src/components/snackbar';
// route
import { paths } from 'src/routes/paths';
// axios
import axios from 'axios';

// ----------------------------------------------------------------------

const SPEED_VALUE = [
  {
    value: 0.7,
    label: 'Tốc độ chậm hơn 0.7 lần',
  },
  {
    value: 0.8,
    label: 'Tốc độ chậm hơn 0.8 lần',
  },
  {
    value: 0.9,
    label: 'Tốc độ chậm hơn 0.9 lần',
  },
  {
    value: 1,
    label: 'Tốc độ đọc bình thường',
  },
  {
    value: 1.1,
    label: 'Tốc độ nhanh hơn 1.1 lần',
  },
  {
    value: 1.2,
    label: 'Tốc độ nhanh hơn 1.2 lần',
  },
  {
    value: 1.3,
    label: 'Tốc độ nhanh hơn 1.3 lần',
  },
];

const DOWNLOAD_TYPE = [
  {
    value: 2,
    label: 'Định dạng file .wav',
  },
  {
    value: 3,
    label: 'Định dạng file .mp3',
  },
];

export default function Page() {
  const { enqueueSnackbar } = useSnackbar();

  const [audioSource, setAudioSource] = useState<string>('');

  const [voices, setVoices] = useState<any>([]);

  useEffect(() => {
    const handleFetchVoices = async () => {
      const { data } = await axios.get('https://viettelgroup.ai/voice/api/tts/v1/rest/voices');
      if (data) {
        setVoices(data);
      }
    };
    handleFetchVoices();
  }, []);

  const Schema = Yup.object().shape({
    text: Yup.string().required('Bạn chưa nhập văn bản chuyển đổi !'),
    voice: Yup.string().required('Bạn chưa chọn giọng đọc !'),
    without_filter: Yup.boolean().required('Bạn chưa chọn chất lượng giọng !'),
    speed: Yup.number().required('Bạn chưa chọn tốc độ !'),
    tts_return_option: Yup.number().required('Bạn chưa chọn định dạng tải xuống !'),
  });

  const defaultValues = useMemo(
    () => ({
      text: `Xin chào bạn`,
      voice: 'hn-quynhanh',
      without_filter: false,
      speed: 1,
      tts_return_option: 3,
    }),
    []
  );

  const methods = useForm({
    resolver: yupResolver(Schema),
    defaultValues,
  });

  const {
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const onSubmit = handleSubmit(async (data: any) => {
    setAudioSource('');

    try {
      const response = (await axios.post(
        'https://viettelgroup.ai/voice/api/tts/v1/rest/syn',
        data,
        {
          headers: {
            'Content-Type': 'application/json',
            token: 'anonymous',
            // token: VIETTEL_API_KEY,
          },
          responseType: 'blob',
        }
      )) as any;

      const blob = new Blob([response.data], { type: 'audio/wav' });

      const audioUrl = URL.createObjectURL(blob);

      setAudioSource(audioUrl);

      enqueueSnackbar('Chuyển đổi thành công thành giọng nói !');
    } catch (error) {
      enqueueSnackbar('Đã có lỗi xảy ra ! Vui lòng thử lại !', { variant: 'error' });
    }
  });

  return (
    <>
      <Helmet>
        <title> Chuyển đổi văn bản thành giọng nói </title>
      </Helmet>

      <CustomBreadcrumbs
        heading="Văn bản thành giọng nói"
        links={[
          { name: 'Tiện ích' },
          {
            name: 'Chuyển đổi',
            href: paths.home.convert.root,
          },
          { name: 'Văn bản thành giọng nói' },
        ]}
        sx={{ mb: { xs: 3, md: 5 } }}
      />

      <Card sx={{ padding: 3 }}>
        <FormProvider methods={methods} onSubmit={onSubmit}>
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <Stack spacing={2}>
                <BlockItem label="Văn bản chuyển đổi :" required>
                  <RHFTextField
                    name="text"
                    placeholder="Nhập vào văn bản muốn chuyển đổi thành giọng nói..."
                    multiline
                    rows={10}
                    sx={{ width: '100%' }}
                  />
                </BlockItem>

                {audioSource && (
                  <Paper
                    elevation={3}
                    sx={{
                      padding: 1,
                      display: 'flex',
                      justifyContent: 'center',
                      alignContent: 'center',
                    }}
                  >
                    <audio controls autoPlay style={{ width: '100%' }}>
                      <track kind="captions" />
                      <source src={audioSource} type="audio/mpeg" />
                      Trình duyệt của bạn không hỗ trợ
                    </audio>
                  </Paper>
                )}
              </Stack>
            </Grid>

            <Grid item xs={12} md={6}>
              <Stack spacing={2} height="100%" justifyContent="space-between">
                <BlockItem label="Lựa chọn ngôn ngữ nói :" required>
                  <RHFSelect name="voice" sx={{ width: '100%' }}>
                    {voices?.map((voice: any) => (
                      <MenuItem key={voice?.code} value={voice?.code}>
                        {`${voice.name} (${voice.description})`}
                      </MenuItem>
                    ))}
                  </RHFSelect>
                </BlockItem>

                <Stack direction="row" justifyContent="space-between" spacing={2}>
                  <BlockItem label="Chất lượng âm thanh :" sx={{ width: '100%' }} required>
                    <RHFSwitch name="without_filter" label="Chất lượng cao cấp" />
                  </BlockItem>

                  <BlockItem label="Tốc độ đọc :" sx={{ width: '100%' }} required>
                    <RHFSelect name="speed" sx={{ width: '100%' }}>
                      {SPEED_VALUE?.map((speed: any) => (
                        <MenuItem key={speed?.value} value={speed?.value}>
                          {speed.label}
                        </MenuItem>
                      ))}
                    </RHFSelect>
                  </BlockItem>

                  <BlockItem label="Định dạng file :" sx={{ width: '100%' }}>
                    <RHFSelect name="tts_return_option" sx={{ width: '100%' }}>
                      {DOWNLOAD_TYPE?.map((type: any) => (
                        <MenuItem key={type?.value} value={type?.value}>
                          {type.label}
                        </MenuItem>
                      ))}
                    </RHFSelect>
                  </BlockItem>
                </Stack>

                <LoadingButton
                  type="submit"
                  size="large"
                  color="primary"
                  variant="contained"
                  loading={isSubmitting}
                >
                  CHUYỂN VĂN BẢN NÀY THÀNH GIỌNG NÓI
                </LoadingButton>
              </Stack>
            </Grid>
          </Grid>
        </FormProvider>
      </Card>
    </>
  );
}
